.visible-none {
  display: none;
}

#project-component {
  margin: 0px 50px 0px 150px;
}

#pj-component {
  margin-bottom: 50px;
}
#pj-component .pj-component--title {
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(192, 192, 213);
  box-shadow: 3px 2px 0px 0px rgb(192, 192, 213);
  border-radius: 10px;
  padding: 20px 50px;
  margin: 50px 3px 20px 0px;
}
#pj-component .pj-component--title h1 {
  margin: 10px 0px 13px 0px;
  font-size: 40px;
}
#pj-component .pj-component--title span {
  font-size: 23px;
}
#pj-component .pj-component--skill div {
  display: flex;
  flex-direction: column;
  border-left: 8px solid rgb(192, 192, 213);
  padding: 20px 15px;
  margin-bottom: 30px;
  border-radius: 4px;
}
#pj-component .pj-component--skill span {
  font-size: 23px;
  font-weight: bold;
}

@media (max-width: 1000px) {
  #project-component {
    margin: 0px 20px;
  }
  #pj-component .pj-component--title h1 {
    font-size: 35px;
  }
  #pj-component .pj-component--title span {
    font-size: 20px;
  }
}
@media (max-width: 740px) {
  #project-component {
    margin: 0px 20px;
  }
  #pj-component {
    margin-bottom: 0px;
  }
  #pj-component .pj-component--title {
    padding: 17px 35px;
  }
  #pj-component .pj-component--title h1 {
    font-size: 30px;
  }
  #pj-component .pj-component--title span {
    font-size: 18px;
  }
  #pj-component .pj-component--skill span {
    font-size: 18px;
  }
}
@media (max-width: 450px) {
  #project-component {
    margin: 0px 20px;
  }
  #pj-component .pj-component--title {
    padding: 15px 10px;
  }
  #pj-component .pj-component--title h1 {
    font-size: 22px;
    text-align: center;
  }
  #pj-component .pj-component--title span {
    font-size: 15px;
  }
  #pj-component .pj-component--skill span {
    font-size: 15px;
  }
}